@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --main-text-color: #444444;
  --accent-text-color: #000000;
  --additional-text-color: #222222;
  --action-color: #ffffff;

  --main-bg-color: #68b738;
  --accent-bg-color: #f2f2f2;
  --border-color: #cdcdcd;
}

.page {
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  color: var(--main-text-color);
}

.page__centerer {
  margin: 0 20px;

  @media (--middle-vp) {
    margin: 0 30px;
  }

  @media (--wide-vp) {
    margin: 0 auto;
    width: 1220px;
  }

  &--nav-menu_mob-position {
    margin: 0;
  }

  &--form_padding {
    @media (--middle-vp) {
      padding: 0 40px;
    }
  }

  &--footer_position {
    display: flex;
    flex-direction: column;
  }
}

@custom-media --small-vp (width <= 767.9px);

@custom-media --not-small-vp (width >= 768px);

@custom-media --middle-vp (768px <= width < 1279.9px);

@custom-media --wide-vp (width >= 1280px);

@custom-media --x2 (min-resolution: 2dppx);

img {
  /*width: 100%;*/
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

:focus {
  outline: 2px solid var(--accent-text-color);
  outline-offset: 2px;

  @supports selector(:focus-visible) {
    outline: 0;
  }
}

:focus-visible {
  outline: 2px solid var(--accent-text-color);
  outline-offset: 2px;
}

.button {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--action-color);
  background-color: var(--main-bg-color);
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: none;
  padding: 10px 0;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    color: rgb(255, 255, 255, 0.3);
  }

  &--show-all_color {
    color: var(--main-text-color);
    background-color: var(--accent-bg-color);

    &:hover {
      background-color: #ebebeb;
    }

    &:active {
      color: rgb(68, 68, 68, 0.3);
    }
  }
}

.field {
  border: none;
}

.field__contacts-legend {
  padding: 0;
  margin-bottom: 35px;
  position: relative;

  @media (--not-small-vp) {
    margin-bottom: 76px;
  }

  &::after {
    @media (--small-vp) {
      top: 16px;
      transform: translateX(20px);
    }

    @media (--middle-vp) {
      transform: translateX(42px);
    }
  }
}

.field__extra-legend {
  padding: 0;
  margin-bottom: 39px;
  position: relative;

  @media (--not-small-vp) {
    margin-bottom: 64px;
  }

  @media (--wide-vp) {
    margin-bottom: 60px;
  }

  &::after {
    @media (--small-vp) {
      top: 15px;
      transform: translateX(23px);
    }

    @media (--middle-vp) {
      transform: translateX(41px);
    }
  }
}

.legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--inner-title-size);
  line-height: 26px;
  color: var(--accent-text-color);
  display: block;
  position: relative;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    line-height: 1;
  }

  &::after {
    content: "";
    position: absolute;
    left: 100%;
    width: 100vw;
    height: 2px;
    background-color: var(--main-bg-color);
    z-index: -1;

    @media (--not-small-vp) {
      top: 22px;
    }

    @media (--wide-vp) {
      transform: translateX(40px);
    }
  }

  &::before {
    @media (--not-small-vp) {
      content: "";
      position: absolute;
      top: 22px;
      right: 100%;
      width: 100vw;
      height: 2px;
      background-color: var(--main-bg-color);
      transform: translateX(-40px);
      z-index: -1;
    }
  }
}

.label {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
  min-width: fit-content;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}

.input {
  font-weight: 400;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #e7e7e7;
  appearance: textfield;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }

  &:hover {
    border-color: var(--border-color);
  }

  &:focus {
    outline: 2px solid var(--main-text-color);
    outline-offset: -2px;
  }

  &::placeholder {
    display: block;
    font-family: var(--accent-font);
    line-height: 18px;
    color: var(--main-text-color);
    text-transform: uppercase;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
}

.title {
  --title-size: 36px;
  --title-big-size: 60px;
  --inner-title-size: 24px;

  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--accent-text-color);
}

.description {
  --description-size: 14px;
  --description-size-large: 16px;

  font-weight: 400;
  font-size: var(--description-size);
  line-height: 18px;
}

.header {
  @media (--wide-vp) {
    padding-top: 46px;
  }
}

.header__wrapper {
  padding-top: 11px;
  margin-bottom: 16px;

  @media (--not-small-vp) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 17px;
    margin-bottom: 54px;
  }

  @media (--wide-vp) {
    padding-top: 0;
    margin-bottom: 65px;
  }
}

.header-logo {
  display: flex;
  align-items: center;
  text-decoration: none;

  @media (--small-vp) {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header-logo__img {
  display: block;

  @media (--middle-vp) {
    width: 62px;
    height: 50px;
    margin-right: 10px;
  }

  @media (--wide-vp) {
    width: 70px;
    height: 59px;
    margin-right: 14px;
  }
}

.header-logo__text {
  padding-top: 6px;

  @media (--small-vp) {
    margin: 0 auto;
    width: 101px;
    height: 19px;
  }

  @media (--not-small-vp) {
    width: 102px;
    height: 19px;
    --fill: #444444;
  }

  @media (--wide-vp) {
    width: 118px;
    height: 21px;
  }
}

.footer {
  background-color: var(--accent-bg-color);

  &--location_bg {
    --bg-color: var(--action-color);
  }
}

.footer__location {
  background-color: var(--bg-color, var(--accent-bg-color));
}

.footer-info {
  padding: 39px 0 22px;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 56px 0 55px;
  }
}

.footer-info__logo-text {
  display: block;

  @media (--small-vp) {
    margin: 0 auto 53px;
  }

  @media (--not-small-vp) {
    padding-top: 8px;
  }

  &:hover {
    fill-opacity: 0.8;
  }

  &:active {
    fill-opacity: 0.6;
  }
}

.footer__link {
  color: var(--main-text-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;

  @media (--not-small-vp) {
    justify-content: flex-end;
  }

  @media (--wide-vp) {
    padding-top: 3px;
  }
}

.footer__link:active {
  opacity: 0.3;
}

.footer__link--icon_htmlacademy::after {
  content: "";
  width: 27px;
  height: 34px;
  background-color: #666666;
  mask: url("../img/logo.svg#htmlacademy") no-repeat center / cover;

  @media (--not-small-vp) {
    margin-left: 24px;
  }
}

.footer__link--icon_htmlacademy:hover::after {
  background-color: var(--main-bg-color);
}

.main-nav {
  width: auto;

  &--closed {
    --icon-menu: url("../icons/sprite.svg#burger");
    --nav-display: none;
  }

  &--opened {
    --icon-menu: url("../icons/sprite.svg#cross");
  }

  &--nojs {
    --visibility-toggle: hidden;
    --nav-position: static;
  }

  &--color_reshuffle {
    @media (--wide-vp) {
      --link-lightness: 100%;
      --underline-color: currentColor;
    }
  }
}

.main-nav__toggle {
  position: absolute;
  z-index: 4;
  top: 20px;
  right: 20px;
  border: none;
  background: initial;
  visibility: var(--visibility-toggle, visible);

  @media (--not-small-vp) {
    --visibility-toggle: hidden;
  }

  &:hover {
    --icon-opacity: 0.6;
  }

  &:active {
    --icon-opacity: 0.3;
  }

  &::before {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background: var(--icon-menu) no-repeat;
    opacity: var(--icon-opacity, 1);
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: var(--nav-display, revert);
  background-color: var(--action-color);

  @media (--small-vp) {
    position: var(--nav-position, absolute);
    top: 48px;
    right: 0;
    left: 0;
    z-index: 3;
    padding-top: 17px;
  }

  @media (--not-small-vp) {
    --nav-display: flex;

    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }
}

.main-nav__item {
  @media (--small-vp) {
    border-top: 1px solid #e6e6e6;
  }

  @media (--not-small-vp) {
    margin-right: 38px;
  }

  @media (--wide-vp) {
    margin-right: 33px;
  }

  &:first-child {
    @media (--not-small-vp) {
      margin-right: 33px;
    }

    @media (--wide-vp) {
      margin-right: 36px;
    }
  }

  &:last-child {
    @media (--small-vp) {
      border-bottom: 1px solid #e6e6e6;
    }

    @media (--not-small-vp) {
      margin-right: 0;
    }
  }
}

.main-nav__link {
  position: relative;
  display: block;
  padding: 25px 0 21px;
  line-height: 1;
  font-size: 20px;
  font-weight: 500;
  font-family: var(--accent-font);
  color: hsl(0deg 0% var(--link-lightness, 0%) / var(--link-alpha, 1));
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  @media (--not-small-vp) {
    line-height: 24px;
    font-size: 18px;
    font-weight: 400;
  }

  @media (--wide-vp) {
    line-height: 30px;
    font-size: 20px;
  }

  &:hover {
    --link-alpha: 0.6;
  }

  &:active {
    --link-alpha: 0.3;
  }

  &[aria-current="page"] {
    text-decoration: underline 2px var(--underline-color, var(--main-bg-color));
    text-underline-offset: 8px;
  }
}

.offer {
  --url-cat-s: url("../img/main-bg-s@1x.jpg");
  --url-cat-l: url("../img/main-bg-l@1x.jpg");
  --url-box-s: url("../img/catenergy-box-s@1x.png");
  --url-box-m: url("../img/catenergy-box-m@1x.png");
  --url-box-l: url("../img/catenergy-box-l@1x.png");

  @media (--middle-vp) {
    background: var(--action-color);
    padding-left: 87px;
  }

  @media (--wide-vp) {
    position: static;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-left: 80px;
    background: none;
  }
}

.offer::before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  min-height: 302px;
  z-index: -1;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    #4f4e53 var(--url-cat-s) no-repeat center;

  @media (--x2) {
    --url-cat-s: url("../img/main-bg-s@2x.jpg");
    background-size: 320px auto;
  }

  @media (--middle-vp) {
    background: none;
  }

  @media (--wide-vp) {
    top: 0;
    --url-cat-s: url("../img/main-bg-l@1x.jpg");
    min-height: 694px;
    background-position: top 0 left -20px;
    left: 50%;
  }

  @media (--x2) {
    --url-cat-l: url("../img/main-bg-l@2x.jpg");
    background-size: 1044px auto;
  }
}

.offer__chapter {
  display: flex;
  flex-direction: column;
  min-height: 480px;
  margin-bottom: 20px;
  position: relative;

  @media (--middle-vp) {
    min-height: 920px;
    order: 2;
    margin-bottom: 0;
  }

  @media (--wide-vp) {
    min-height: 455px;
    padding-right: 65px;
    margin-bottom: 82px;
  }
}

.offer__chapter::after {
  position: absolute;
  content: "";
  top: 167px;
  left: 50%;
  transform: translateX(-50%);
  width: 280px;
  height: 270px;
  background: var(--url-box-s) no-repeat;
  background-size: contain;

  @media (--x2) {
    --url-box-s: url("../img/catenergy-box-s@2x.png");
  }

  @media (--middle-vp) {
    top: 201px;
    width: 709px;
    height: 609px;
    transform: translateX(-56%);
    background: var(--url-box-m) no-repeat;
    background-size: cover;

    @media (--x2) {
      --url-box-m: url("../img/catenergy-box-m@2x.png");
    }
  }

  @media (--wide-vp) {
    position: absolute;
    transform: translateX(0);
    width: 552px;
    height: 499px;
    top: -82px;
    left: 368px;
    background: var(--url-box-l) no-repeat;
    background-size: cover;
  }

  @media (--x2) {
    --url-box-l: url("../img/catenergy-box-l@2x.png");
  }
}

.offer__title {
  font-size: var(--title-size);
  line-height: 36px;
  color: var(--action-color);
  text-align: center;
  padding: 27px 0 0 0;
  margin: 0 0 25px 0;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
    padding-top: 0;
    margin-bottom: 41px;
  }

  @media (--wide-vp) {
    padding-top: 0;
    margin-bottom: 40px;
  }
}

.offer__text {
  font-family: var(--accent-font);
  line-height: 1;
  color: var(--action-color);
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0 0 49px 0;
  z-index: 1;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
  }

  @media (--wide-vp) {
    margin: 0 0 52px;
  }
}

.offer__button {
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  min-width: 100%;
  padding: 11px 0;
  margin-top: auto;
  z-index: 1;

  @media (--not-small-vp) {
    font-size: 20px;
    min-width: 244px;
    padding: 14px 0;
    margin: 0;
  }
}

.options__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--middle-vp) {
    margin-bottom: 45px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
    margin-bottom: 71px;
  }
}

.options__item {
  padding: 21px 33px 18px 19px;
  background-color: var(--accent-bg-color);
  margin-bottom: 20px;

  @media (--middle-vp) {
    padding: 77px 62px 57px 52px;
    margin-bottom: 30px;
  }

  @media (--wide-vp) {
    padding: 77px 51px 55px 52px;
  }

  &--icon_tiger {
    @media (--wide-vp) {
      padding-right: 61px;
    }
  }
}

.options__title {
  font-size: var(--inner-title-size);
  line-height: 37px;
  text-transform: uppercase;
  position: relative;
  padding: 0 0 0 79px;
  margin: 0;
  margin-bottom: 29px;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    line-height: 1;
    padding: 0;
    margin-bottom: 24px;
  }

  @media (--wide-vp) {
    padding-left: 162px;
    margin-bottom: 74px;
  }
}

.options__title--has_icon::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 0;
  width: 70px;
  height: 70px;

  @media (--middle-vp) {
    top: -26px;
    right: -50px;
    left: auto;
    width: 280px;
    height: 280px;
  }

  @media (--wide-vp) {
    top: -50px;
    left: -20px;
    width: 140px;
    height: 140px;
  }
}

.options__title--icon_slimming::before {
  background: url("../icons/sprite.svg#slimming") no-repeat;
}

.options__title--icon_tiger::before {
  background: url("../icons/sprite.svg#tiger") no-repeat;
}

.options__description {
  padding: 0;
  margin: 0;
  margin-bottom: 38px;
  position: relative;

  @media (--not-small-vp) {
    font-size: var(--description-size-large);
    line-height: 24px;
    color: var(--accent-text-color);
    max-width: 340px;
    margin-bottom: 52px;
  }

  @media (--wide-vp) {
    max-width: 100%;
    margin-bottom: 23px;
  }

  &::after {
    @media (--small-vp) {
      content: "";
      position: absolute;
      bottom: -23px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #d9d9d9;
    }
  }
}

.options__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--additional-text-color);
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  text-decoration: none;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }

  &:active {
    opacity: 0.3;
  }

  &--icon_arrow::after {
    content: "";
    width: 32px;
    height: 10px;
    margin-left: 15px;
    background: url("../icons/sprite.svg#arrow") no-repeat center / cover;
    background-position: -8px;
  }

  &--icon_arrow:hover::after {
    background-position: 0;
    transition: all 0.2s ease-in-out;
  }
}

.process__chapter {
  margin-bottom: 49px;

  @media (--middle-vp) {
    margin-bottom: 147px;
  }

  @media (--wide-vp) {
    margin-bottom: 126px;
  }
}

.process__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 1;
    margin-bottom: 72px;
  }
}

.process__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 134px 60px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.process__item {
  @media (--not-small-vp) {
    position: relative;
    margin-bottom: 0;

    &--number::before {
      position: absolute;
      top: -40px;
      right: 71px;
      font-family: var(--accent-font);
      font-size: 280px;
      line-height: 1;
      color: var(--accent-bg-color);

      @media (--wide-vp) {
        right: 0;
      }
    }

    &--number_one::before {
      content: "1";
    }

    &--number_two::before {
      content: "2";
    }

    &--number_three::before {
      content: "3";
    }

    &--number_four::before {
      content: "4";
    }
  }
}

.process__item:not(:last-child) {
  @media (--small-vp) {
    margin-bottom: 20px;
  }
}

.process__description {
  min-height: 60px;
  padding-left: 81px;
  margin: 0;
  position: relative;

  @media (--not-small-vp) {
    font-size: var(--description-size-large);
    line-height: 24px;
    padding: 111px 0 0 0;
    max-width: 245px;
  }

