  margin: 0;

  @media (--not-small-vp) {
    font-size: 16px;
  }

  &-mass {
    text-align: right;
    padding-right: 2px;

    @media (--not-small-vp) {
      padding: 7px 2px 8px 0;
      border-bottom: 1px solid var(--border-color);
    }

    @media (--wide-vp) {
      padding-top: 11px;
      margin: 0 4px;
    }
  }

  &-mass::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    @media (--not-small-vp) {
      padding: 7px 0 8px;
    }

    @media (--wide-vp) {
      padding-top: 11px;
    }
  }

  &-taste {
    text-align: right;
    top: -2px;

    @media (--not-small-vp) {
      padding: 9px 0 6px;
      border-bottom: 1px solid var(--border-color);
    }

    @media (--wide-vp) {
      padding-top: 10px;
      margin: 0 4px;
    }
  }

  &-taste::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    @media (--not-small-vp) {
      padding: 9px 0 6px;
    }
  }

  &-price {
    text-align: right;
    top: -3px;

    @media (--not-small-vp) {
      padding: 10px 0 8px;
      border-bottom: 1px solid var(--border-color);
    }

    @media (--wide-vp) {
      padding-top: 9px;
      top: 0;
      margin: 0 4px;
    }
  }

  &-price::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    @media (--not-small-vp) {
      padding: 9px 0 8px;
    }
  }

  &--text_position {
    text-align: center;
    margin-bottom: 21px;

    @media (--not-small-vp) {
      line-height: 18px;
      width: 170px;
      margin-bottom: 10px;
    }
  }
}

.table__image {
  display: block;
  text-align: center;
  grid-area: 1 / 1 / 5 / 2;

  @media (--not-small-vp) {
    grid-row: 1 / 2;
  }
}

.table__image-link {
  display: block;
}

.table__button {
  grid-column: 1 / -1;
  padding-top: 17px;
  margin-top: auto;

  @media (--not-small-vp) {
    padding-top: 29px;
  }

  @media (--wide-vp) {
    margin: 0 4px;
  }
}

.add-goods__wrapper {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
    margin-bottom: 84px;
  }
}

.add-goods__table {
  display: grid;
  grid-template-columns: 1fr;
  margin-bottom: 34px;

  @media (--not-small-vp) {
    margin-bottom: 68px;
  }

  @media (--wide-vp) {
    grid-column: 1 / 4;
    margin-bottom: 0;
  }
}

.add-goods__title {
  font-size: var(--inner-title-size);
  line-height: 1;
  display: inline-block;
  padding: 0;
  margin: 0 0 35px;
  position: relative;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    margin: 0 0 80px 37px;
  }

  @media (--wide-vp) {
    margin: 0 0 81px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 15px;
    left: 100%;
    width: 100vw;
    height: 2px;
    transform: translateX(22px);
    background-color: var(--main-bg-color);

    @media (--not-small-vp) {
      top: 22px;
      transform: translateX(36px);
    }

    @media (--wide-vp) {
      transform: translateX(35px);
    }
  }

  &::before {
    @media (--not-small-vp) {
      content: "";
      position: absolute;
      top: 22px;
      right: 100%;
      width: 100vw;
      height: 2px;
      transform: translateX(-37px);
      background-color: var(--main-bg-color);
    }

    @media (--wide-vp) {
      transform: translateX(-40px);
    }
  }
}

.add-table__line {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
  padding: 13px 0 18px;

  &:not(:last-child) {
    border-bottom: none;
  }


  @media (--middle-vp) {
    grid-template-columns: repeat(4, 1fr);
    gap: 0 60px;
    border: 1px solid var(--border-color);
    padding: 26px 38px 28px;
  }

  @media (--wide-vp) {
    grid-template-columns: 325px 245px 80px 245px;
    width: 895px;
    align-items: center;
    padding: 15px 0 16px;
  }
}

.add-table__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--additional-text-color);
  text-transform: uppercase;
  padding: 0 0 13px;
  grid-column: 1 / 3;
  text-align: left;

  @media (--not-small-vp) {
    font-size: 20px;
    padding-bottom: 24px;
  }

  @media (--wide-vp) {
    grid-column: 1 / 2;
    padding-bottom: 0;
  }
}

.add-table__cell {
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 1;
  padding: 0;
  margin-bottom: 15px;

  @media (--not-small-vp) {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 0;
  }

  &--volume {
    @media (--middle-vp) {
      text-align: right;
      padding-top: 3px;
      margin-right: -80px;
    }

    @media (--wide-vp) {
      text-align: left;
      padding-top: 6px;
    }
  }

  &--prie {
    text-align: right;

    @media (--not-small-vp) {
      padding-top: 3px;
    }

    @media (--wide-vp) {
      text-align: left;
    }
  }
}

.add-table__button {
  grid-column: 1 / -1;
  margin: 0;

  @media (--wide-vp) {
    grid-column: 4 / 5;
  }
}

.add-goods__button {
  display: block;
  width: 100%;
}

.present__chapter {
  min-height: 290px;
  margin-bottom: 35px;
  position: relative;
  background: url("../icons/sprite.svg#present") no-repeat top 59px center;
  background-size: 78px 80px;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 60px;
    background-position: 79px center;
    min-height: 200px;
    margin-bottom: 76px;
  }

  @media (--wide-vp) {
    background-position: top 57px center;
    min-height: 100%;
    margin-bottom: 0;
  }
}

.present__chapter::before {
  --url-cat-case: url("../img/cat-case-s@1x.jpg");

  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    var(--url-cat-case) no-repeat center;
  background-size: cover;
  z-index: -1;

  @media (--x2) {
    --url-cat-case: url("../img/cat-case-s@2x.jpg");
  }

  @media (--middle-vp) {
    --url-cat-case: url("../img/cat-case-m@1x.jpg");

    @media (--x2) {
      --url-cat-case: url("../img/cat-case-m@2x.jpg");
    }
  }

  @media (--wide-vp) {
    grid-column: 4 / 5;
    --url-cat-case: url("../img/cat-case-l@1x.jpg");

    @media (--x2) {
      --url-cat-case: url("../img/cat-case-l@2x.jpg");
    }
  }
}

.present__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--action-color);
  text-align: center;
  max-width: 161px;
  padding: 187px 0 0;
  margin: 0 auto;

  @media (--middle-vp) {
    grid-column: 2/3;
    text-align: left;
    padding-top: 0;
    margin: auto auto auto 39px;
  }
}

.social-list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0 8px 0 0;
  margin: 0;
  position: relative;

  @media (--small-vp) {
    margin-bottom: 43px;
  }

  @media (--not-small-vp) {
    padding-right: 18px;
  }

  @media (--small-vp) {
    &::before,
    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 1px;
      background-color: #d9d9d9;
    }

    &::before {
      top: -22px;
    }

    &::after {
      bottom: -22px;
    }
  }
}

.social-list__item:not(:last-child) {
  margin-right: 34px;

  @media (--not-small-vp) {
    margin-right: 25px;
  }
}

.social-list__link {
  display: flex;
  text-decoration: none;
}

.social-list__link--icon::before {
  content: "";
  background-color: #666666;
  mask: var(--icon) no-repeat center / cover;
}

.social-list__link--icon:hover::before {
  background-color: var(--main-bg-color);
}

.social-list__link--icon:active::before {
  background-color: var(--main-bg-color);
  opacity: 0.3;
}

.social-list__link--vkontakte::before {
  --icon: url("../icons/sprite.svg#vkontakte");
  width: 25px;
  height: 16px;

  @media (--not-small-vp) {
    width: 34px;
    height: 28px;
  }
}

.social-list__link--instagram::before {
  --icon: url("../icons/sprite.svg#instagram");
  width: 18px;
  height: 18px;

  @media (--not-small-vp) {
    width: 25px;
    height: 25px;
  }
}

.social-list__link--facebook::before {
  --icon: url("../icons/sprite.svg#facebook");
  width: 18px;
  height: 20px;

  @media (--not-small-vp) {
    width: 20px;
    height: 30px;
  }
}

.social-list__icon {
  @media (--not-small-vp) {
    width: 29px;
    height: 34px;
  }
}
