@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --main-text-color: #444444;
  --accent-text-color: #000000;
  --additional-text-color: #222222;
  --action-color: #ffffff;
  --main-bg-color: #68b738;
  --accent-bg-color: #f2f2f2;
  --border-color: #cdcdcd;
}

.page {
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  color: var(--main-text-color);
}

.page__centerer {
  margin: 0 20px;
}
@media (--middle-vp) {
  .page__centerer {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}
.page__centerer--nav-menu_mob-position {
  margin: 0;
}
@media (--middle-vp) {
  .page__centerer--form_padding {
    padding: 0 40px;
  }
}
.page__centerer--footer_position {
  display: flex;
  flex-direction: column;
}

@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --x2 (min-resolution: 2dppx);
img {
  /*width: 100%;*/
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

:focus {
  outline: 2px solid var(--accent-text-color);
  outline-offset: 2px;
}
@supports selector(:focus-visible) {
  :focus {
    outline: 0;
  }
}

:focus-visible {
  outline: 2px solid var(--accent-text-color);
  outline-offset: 2px;
}

.button {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--action-color);
  background-color: var(--main-bg-color);
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: none;
  padding: 10px 0;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--show-all_color {
  color: var(--main-text-color);
  background-color: var(--accent-bg-color);
}
.button--show-all_color:hover {
  background-color: #ebebeb;
}
.button--show-all_color:active {
  color: rgba(68, 68, 68, 0.3);
}

.field {
  border: none;
}

.field__contacts-legend {
  padding: 0;
  margin-bottom: 35px;
  position: relative;
}
@media (--not-small-vp) {
  .field__contacts-legend {
    margin-bottom: 76px;
  }
}
@media (--small-vp) {
  .field__contacts-legend::after {
    top: 16px;
    transform: translateX(20px);
  }
}
@media (--middle-vp) {
  .field__contacts-legend::after {
    transform: translateX(42px);
  }
}

.field__extra-legend {
  padding: 0;
  margin-bottom: 39px;
  position: relative;
}
@media (--not-small-vp) {
  .field__extra-legend {
    margin-bottom: 64px;
  }
}
@media (--wide-vp) {
  .field__extra-legend {
    margin-bottom: 60px;
  }
}
@media (--small-vp) {
  .field__extra-legend::after {
    top: 15px;
    transform: translateX(23px);
  }
}
@media (--middle-vp) {
  .field__extra-legend::after {
    transform: translateX(41px);
  }
}

.legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--inner-title-size);
  line-height: 26px;
  color: var(--accent-text-color);
  display: block;
  position: relative;
}
@media (--not-small-vp) {
  .legend {
    font-size: var(--title-size);
    line-height: 1;
  }
}
.legend::after {
  content: "";
  position: absolute;
  left: 100%;
  width: 100vw;
  height: 2px;
  background-color: var(--main-bg-color);
  z-index: -1;
}
@media (--not-small-vp) {
  .legend::after {
    top: 22px;
  }
}
@media (--wide-vp) {
  .legend::after {
    transform: translateX(40px);
  }
}
@media (--not-small-vp) {
  .legend::before {
    content: "";
    position: absolute;
    top: 22px;
    right: 100%;
    width: 100vw;
    height: 2px;
    background-color: var(--main-bg-color);
    transform: translateX(-40px);
    z-index: -1;
  }
}

.label {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
  min-width: fit-content;
}
@media (--not-small-vp) {
  .label {
    font-size: 20px;
    line-height: 30px;
  }
}

.input {
  font-weight: 400;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #e7e7e7;
  appearance: textfield;
}
@media (--not-small-vp) {
  .input {
    font-size: 20px;
    line-height: 30px;
  }
}
.input:hover {
  border-color: var(--border-color);
}
.input:focus {
  outline: 2px solid var(--main-text-color);
  outline-offset: -2px;
}
.input::placeholder {
  display: block;
  font-family: var(--accent-font);
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
}

.title {
  --title-size: 36px;
  --title-big-size: 60px;
  --inner-title-size: 24px;
  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--accent-text-color);
}

.description {
  --description-size: 14px;
  --description-size-large: 16px;
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 18px;
}

@media (--wide-vp) {
  .header {
    padding-top: 46px;
  }
}

.header__wrapper {
  padding-top: 11px;
  margin-bottom: 16px;
}
@media (--not-small-vp) {
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 17px;
    margin-bottom: 54px;
  }
}
@media (--wide-vp) {
  .header__wrapper {
    padding-top: 0;
    margin-bottom: 65px;
  }
}

.header-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}
@media (--small-vp) {
  .header-logo {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
  }
}
.header-logo:hover {
  opacity: 0.8;
}
.header-logo:active {
  opacity: 0.6;
}

.header-logo__img {
  display: block;
}
@media (--middle-vp) {
  .header-logo__img {
    width: 62px;
    height: 50px;
    margin-right: 10px;
  }
}
@media (--wide-vp) {
  .header-logo__img {
    width: 70px;
    height: 59px;
    margin-right: 14px;
  }
}

.header-logo__text {
  padding-top: 6px;
}
@media (--small-vp) {
  .header-logo__text {
    margin: 0 auto;
    width: 101px;
    height: 19px;
  }
}
@media (--not-small-vp) {
  .header-logo__text {
    width: 102px;
    height: 19px;
    --fill: #444444;
  }
}
@media (--wide-vp) {
  .header-logo__text {
    width: 118px;
    height: 21px;
  }
}

.footer {
  background-color: var(--accent-bg-color);
}
.footer--location_bg {
  --bg-color: var(--action-color);
}

.footer__location {
  background-color: var(--bg-color, var(--accent-bg-color));
}

.footer-info {
  padding: 39px 0 22px;
}
@media (--not-small-vp) {
  .footer-info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 56px 0 55px;
  }
}

.footer-info__logo-text {
  display: block;
}
@media (--small-vp) {
  .footer-info__logo-text {
    margin: 0 auto 53px;
  }
}
@media (--not-small-vp) {
  .footer-info__logo-text {
    padding-top: 8px;
  }
}
.footer-info__logo-text:hover {
  fill-opacity: 0.8;
}
.footer-info__logo-text:active {
  fill-opacity: 0.6;
}

.footer__link {
  color: var(--main-text-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}
@media (--not-small-vp) {
  .footer__link {
    justify-content: flex-end;
  }
}
@media (--wide-vp) {
  .footer__link {
    padding-top: 3px;
  }
}

.footer__link:active {
  opacity: 0.3;
}

.footer__link--icon_htmlacademy::after {
  content: "";
  width: 27px;
  height: 34px;
  background-color: #666666;
  mask: url("../img/logo.svg#htmlacademy") no-repeat center/cover;
}
@media (--not-small-vp) {
  .footer__link--icon_htmlacademy::after {
    margin-left: 24px;
  }
}

.footer__link--icon_htmlacademy:hover::after {
  background-color: var(--main-bg-color);
}

.main-nav {
  width: auto;
}
.main-nav--closed {
  --icon-menu: url("../icons/sprite.svg#burger");
  --nav-display: none;
}
.main-nav--opened {
  --icon-menu: url("../icons/sprite.svg#cross");
}
.main-nav--nojs {
  --visibility-toggle: hidden;
  --nav-position: static;
}
@media (--wide-vp) {
  .main-nav--color_reshuffle {
    --link-lightness: 100%;
    --underline-color: currentColor;
  }
}

.main-nav__toggle {
  position: absolute;
  z-index: 4;
  top: 20px;
  right: 20px;
  border: none;
  background: initial;
  visibility: var(--visibility-toggle, visible);
}
@media (--not-small-vp) {
  .main-nav__toggle {
    --visibility-toggle: hidden;
  }
}
.main-nav__toggle:hover {
  --icon-opacity: 0.6;
}
.main-nav__toggle:active {
  --icon-opacity: 0.3;
}
.main-nav__toggle::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background: var(--icon-menu) no-repeat;
  opacity: var(--icon-opacity, 1);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: var(--nav-display, revert);
  background-color: var(--action-color);
}
@media (--small-vp) {
  .main-nav__list {
    position: var(--nav-position, absolute);
    top: 48px;
    right: 0;
    left: 0;
    z-index: 3;
    padding-top: 17px;
  }
}
@media (--not-small-vp) {
  .main-nav__list {
    --nav-display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }
}

@media (--small-vp) {
  .main-nav__item {
    border-top: 1px solid #e6e6e6;
  }
}
@media (--not-small-vp) {
  .main-nav__item {
    margin-right: 38px;
  }
}
@media (--wide-vp) {
  .main-nav__item {
    margin-right: 33px;
  }
}
@media (--not-small-vp) {
  .main-nav__item:first-child {
    margin-right: 33px;
  }
}
@media (--wide-vp) {
  .main-nav__item:first-child {
    margin-right: 36px;
  }
}
@media (--small-vp) {
  .main-nav__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (--not-small-vp) {
  .main-nav__item:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  position: relative;
  display: block;
  padding: 25px 0 21px;
  line-height: 1;
  font-size: 20px;
  font-weight: 500;
  font-family: var(--accent-font);
  color: hsl(0deg 0% var(--link-lightness, 0%)/var(--link-alpha, 1));
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
}
@media (--not-small-vp) {
  .main-nav__link {
    line-height: 24px;
    font-size: 18px;
    font-weight: 400;
  }
}
@media (--wide-vp) {
  .main-nav__link {
    line-height: 30px;
    font-size: 20px;
  }
}
.main-nav__link:hover {
  --link-alpha: 0.6;
}
.main-nav__link:active {
  --link-alpha: 0.3;
}
.main-nav__link[aria-current=page] {
  text-decoration: underline 2px var(--underline-color, var(--main-bg-color));
  text-underline-offset: 8px;
}

.offer {
  --url-cat-s: url("../img/main-bg-s@1x.jpg");
  --url-cat-l: url("../img/main-bg-l@1x.jpg");
  --url-box-s: url("../img/catenergy-box-s@1x.png");
  --url-box-m: url("../img/catenergy-box-m@1x.png");
  --url-box-l: url("../img/catenergy-box-l@1x.png");
}
@media (--middle-vp) {
  .offer {
    background: var(--action-color);
    padding-left: 87px;
  }
}
@media (--wide-vp) {
  .offer {
    position: static;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-left: 80px;
    background: none;
  }
}

.offer::before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  min-height: 302px;
  z-index: -1;
  background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), #4f4e53 var(--url-cat-s) no-repeat center;
}
@media (--x2) {
  .offer::before {
    --url-cat-s: url("../img/main-bg-s@2x.jpg");
    background-size: 320px auto;
  }
}
@media (--middle-vp) {
  .offer::before {
    background: none;
  }
}
@media (--wide-vp) {
  .offer::before {
    top: 0;
    --url-cat-s: url("../img/main-bg-l@1x.jpg");
    min-height: 694px;
    background-position: top 0 left -20px;
    left: 50%;
  }
}
@media (--x2) {
  .offer::before {
    --url-cat-l: url("../img/main-bg-l@2x.jpg");
    background-size: 1044px auto;
  }
}

.offer__chapter {
  display: flex;
  flex-direction: column;
  min-height: 480px;
  margin-bottom: 20px;
  position: relative;
}
@media (--middle-vp) {
  .offer__chapter {
    min-height: 920px;
    order: 2;
    margin-bottom: 0;
  }
}
@media (--wide-vp) {
  .offer__chapter {
    min-height: 455px;
    padding-right: 65px;
    margin-bottom: 82px;
  }
}

.offer__chapter::after {
  position: absolute;
  content: "";
  top: 167px;
  left: 50%;
  transform: translateX(-50%);
  width: 280px;
  height: 270px;
  background: var(--url-box-s) no-repeat;
  background-size: contain;
}
@media (--x2) {
  .offer__chapter::after {
    --url-box-s: url("../img/catenergy-box-s@2x.png");
  }
}
@media (--middle-vp) {
  .offer__chapter::after {
    top: 201px;
    width: 709px;
    height: 609px;
    transform: translateX(-56%);
    background: var(--url-box-m) no-repeat;
    background-size: cover;
  }
}
@media (--middle-vp) and (--x2) {
  .offer__chapter::after {
    --url-box-m: url("../img/catenergy-box-m@2x.png");
  }
}
@media (--wide-vp) {
  .offer__chapter::after {
    position: absolute;
    transform: translateX(0);
    width: 552px;
    height: 499px;
    top: -82px;
    left: 368px;
    background: var(--url-box-l) no-repeat;
    background-size: cover;
  }
}
@media (--x2) {
  .offer__chapter::after {
    --url-box-l: url("../img/catenergy-box-l@2x.png");
  }
}

.offer__title {
  font-size: var(--title-size);
  line-height: 36px;
  color: var(--action-color);
  text-align: center;
  padding: 27px 0 0 0;
  margin: 0 0 25px 0;
}
@media (--not-small-vp) {
  .offer__title {
    font-size: var(--title-big-size);
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
    padding-top: 0;
    margin-bottom: 41px;
  }
}
@media (--wide-vp) {
  .offer__title {
    padding-top: 0;
    margin-bottom: 40px;
  }
}

.offer__text {
  font-family: var(--accent-font);
  line-height: 1;
  color: var(--action-color);
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0 0 49px 0;
  z-index: 1;
}
@media (--not-small-vp) {
  .offer__text {
    font-size: 20px;
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
  }
}
@media (--wide-vp) {
  .offer__text {
    margin: 0 0 52px;
  }
}

.offer__button {
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  min-width: 100%;
  padding: 11px 0;
  margin-top: auto;
  z-index: 1;
}
@media (--not-small-vp) {
  .offer__button {
    font-size: 20px;
    min-width: 244px;
    padding: 14px 0;
    margin: 0;
  }
}

.options__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (--middle-vp) {
  .options__list {
    margin-bottom: 45px;
  }
}
@media (--wide-vp) {
  .options__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
    margin-bottom: 71px;
  }
}

.options__item {
  padding: 21px 33px 18px 19px;
  background-color: var(--accent-bg-color);
  margin-bottom: 20px;
}
@media (--middle-vp) {
  .options__item {
    padding: 77px 62px 57px 52px;
    margin-bottom: 30px;
  }
}
@media (--wide-vp) {
  .options__item {
    padding: 77px 51px 55px 52px;
  }
}
@media (--wide-vp) {
  .options__item--icon_tiger {
    padding-right: 61px;
  }
}

.options__title {
  font-size: var(--inner-title-size);
  line-height: 37px;
  text-transform: uppercase;
  position: relative;
  padding: 0 0 0 79px;
  margin: 0;
  margin-bottom: 29px;
}
@media (--not-small-vp) {
  .options__title {
    font-size: var(--title-size);
    line-height: 1;
    padding: 0;
    margin-bottom: 24px;
  }
}
@media (--wide-vp) {
  .options__title {
    padding-left: 162px;
    margin-bottom: 74px;
  }
}

.options__title--has_icon::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 0;
  width: 70px;
  height: 70px;
}
@media (--middle-vp) {
  .options__title--has_icon::before {
    top: -26px;
    right: -50px;
    left: auto;
    width: 280px;
    height: 280px;
  }
}
@media (--wide-vp) {
  .options__title--has_icon::before {
    top: -50px;
    left: -20px;
    width: 140px;
    height: 140px;
  }
}

.options__title--icon_slimming::before {
  background: url("../icons/sprite.svg#slimming") no-repeat;
}

.options__title--icon_tiger::before {
  background: url("../icons/sprite.svg#tiger") no-repeat;
}

.options__description {
  padding: 0;
  margin: 0;
  margin-bottom: 38px;
  position: relative;
}
@media (--not-small-vp) {
  .options__description {
    font-size: var(--description-size-large);
    line-height: 24px;
    color: var(--accent-text-color);
    max-width: 340px;
    margin-bottom: 52px;
  }
}
@media (--wide-vp) {
  .options__description {
    max-width: 100%;
    margin-bottom: 23px;
  }
}
@media (--small-vp) {
  .options__description::after {
    content: "";
    position: absolute;
    bottom: -23px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #d9d9d9;
  }
}

.options__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--additional-text-color);
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  text-decoration: none;
}
@media (--not-small-vp) {
  .options__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.options__link:active {
  opacity: 0.3;
}
.options__link--icon_arrow::after {
  content: "";
  width: 32px;
  height: 10px;
  margin-left: 15px;
